import React, { Component } from 'react';

class WorldMap extends Component {
    state = {}
    render() { 
        return (
            <div id="WorldMap">
                <div className="content">
                    <a href="#nav">Do góry</a>
                    <br />
                    <img src="./images/map.jpg" alt="Mapa świata" className="worldmap" />
                </div>
            </div>
        );
    }
}
 
export default WorldMap;