import React, { Component } from 'react';
import $ from 'jquery';

class Story extends Component {
    state = {  }

    componentDidMount() {
        $('.nav.hidden').removeClass('hidden');
        setTimeout(() => {
            $('.content.hidden').removeClass('hidden');
        }, 1000);
    }
    
    render() { 
        return (
            <React.Fragment>
                <div id="story">
                    <div className="content hidden">
                        <div className="content-title">
                            <h1>Prolog</h1>
                        </div>
                        <div className="content-story">
                            <p>&emsp;Twoja historia rozpoczyna się w mieście portowym Luskan, które jest ostatnim posterunkiem przed mroźnymi zakamarkami Doliny Lodowego Wichru. Znane też pod nazwą Miasta Żagli, jest jednym z największych miast Wybrzeża Mieczy oraz jednym z najważniejszych. Jego mieszkańcami są ludzie północy z Ruathrym, w większości byli piraci. W Luskan władzę obejmuje pięciu najwyższych kapitanów, z których każdy był niegdyś lordem wśród piratów. Prawdziwą władzę posiada jednak wspólnota magów zwana Bractwem Tajemnic (lub Tajemnym Bractwem). Jego członkowie starają się unikać konfliktów z Waterdeep i Amn. W 1361 mieszkańcy Luskan podbili ojczyznę ludzi północy, wyspę Ruathrym, lecz wkrótce potem zostali z niej odparci przez Sojusz Lordów. Luskanie nie tylko stracili twarz, lecz poczuli się również oszukani. Z tego powodu nie zwracają uwagi na cumujących na ich wodach piratów żyjących z łupienia Waterdeep.</p>
                            <br />
                            <p>&emsp;Przybyłaś do miasta Luskan wczesną zimą, która przez sąsiedztwo z Doliną Lodowego Wichru jest dosyć sroga. Twoje największe zainteresowanie wzbudza Wieża Tajemnych Arkan, która pomogłaby Ci rozszerzyć wiedzę na temat Twoich badań nad mocą magiczną. Ogromna budowla przypominająca wyglądem potężne drzewo, pnie się wysoko ponad mury miasta dając znak dalekim jeszcze podróżnym, że cel ich destynacji jest już blisko. Widok tej wieży z ulicy zabiera Ci dech w piersiach i wzbudza podniecenie, jednak na tą chwilę musisz znaleźć jakąś gospodę na noc. O tej porze Wieża Tajemnych Arkan jest zamknięta dla odwiedzających. Zatrzymujesz się więc w gospodzie "Królewskie Dłonie", słynącej z wykwintnych potraw i nietuzinkowego wystroju. Tego dnia gospoda wypełniona była miejscową arystokracją i podróżnymi, którzy cenią sobie luksusy i wygodę. Siadasz przy okrągłym, drewnianym stole będącym najbliżej paleniska. Trzaskający wesoło ogień koił Twoje niezwykle czułe uszy, wrażliwe na hałas szczególnie przy takim tłumie. Torbę z zapasami rzuciłaś pod stół i wzięłaś do ręki przypiętą do pasa i schowaną w pokrowiec księgę. Otworzyłaś ją, obserwując towarzystwo w poszukiwaniu potencjalnych ciekawskich, jednak wszyscy byli zajęci sobą. Zaczęłaś czytać. W tym samym momencie przerwała Ci kelnerka, która grzecznie i dostojnie zapytała co podać. Odmachnęłaś jej jedynie ręką dając do zrozumienia, żeby Ci nie przeszkadzała i wgłębiłaś się w lekturę.</p>
                            <br />
                            <p>&emsp;Po godzinie studiowania swoich własnych badań Twoje czułe uszy wyłapały zdanie "Wieża Tajemnych Arkan" z wypowiedzi jakiegoś szlachcica siedzącego dwa stoły za Tobą. Zamknęłaś na chwilę księgę i skupiłaś się na rozmowie dochodzącej właśnie z tamtego stołu. <span className="dialogue">"... dokonać takiej zbrodni. / Na dodatek nikt nic nie wie! Nie wiem jak Wy, Panowie, ale ja się nie czuję bezpieczny w tym mieście. Będę musiał wynająć dodatkowych najemników do ochrony. / Przesadzasz, Nathanie. Coś, co miało związek z wieżą nijak nie łączy się z Tobą. Nie masz się czego obawiać. / Przypomnij sobie ilu teraz strażników stoi pod wieżą. Już nawet ludzie z Bractwa Tajemnic patrolują jej okolicę i szukają potencjalnych śladów. Oj, takim wpaść w ręce. / Szkoda podróżnych. Słyszałem, że tego dnia wielu przybyło by obejrzeć wieżę od środka i postudiować jej wiedzę. / Wielka szkoda."</span> W jednym momencie Twoje plany legly w gruzach. Musisz się dowiedzieć co się stało w wieży, jednak potrzebujesz do tego świeżego umysłu. Zabrałaś więc swoje rzeczy i poszłaś wynając u gospodarza pokój na noc.</p>
                            <br />
                            <p>&emsp;Pokój wynajęty przez Ciebie był bardzo przytulny, jednak nie możesz tego samego powiedzieć o snach, które Cię tej nocy nawiedzały. Wpierw śniły Ci się gobliny, poważnie rozmyślające jak przyrządzić Twoją księgę na obiad, podczas gdy Ty próbowałaś wyłamać kraty antymagicznej klatki używając ręki truposza, który też się tam znajdował. Drugi sen był niewyraźny. Stałaś w ogromnej sali ze śnieżnobiałym sklepieniem. Wokół była nieskończona przestrzeń wypełniona kolumnami. Przed Tobą zaś znajdował się tron z czaszek, przed którym stała zakapturzona postać, podająca Ci notkę, na której były napisane słowa w nieznanym Ci języku. Ostatni sen przedstawiał tajemniczy monolit, który wyrastał z ziemi okrytej grubą warstwą śniegu. Wokoło szaleje burza śnieżna. Próbujesz się do niego zbliżyć, jednak postrzymuje Cię jakaś tajemnicza siła ciągnąca Cię w przeciwnym kierunku. Zza Twoich pleców rozglega się złowrogi śmiech, lecz nie możesz się odwrócić. Tajemnicza postać przemówiła męskim głosem.<br /><span className="dialogue">&emsp;- To jeszcze nie jest Twój czas, moja droga. Teraz śpij! -</span><br />Burza śnieżna się nasiliła i czujesz jak zaczyna Cię zasypywać. Budzisz się gwałtownie.</p>
                            <br />
                            <p>&emsp;Podnosisz się powoli z łóżka i ocierasz pot z czoła. Blade promienie słoneczne wpadają do pomieszczenia rozganiając ciemność. Świeca leżąca na stoliku nocnym musiała zgasnąć przed chwilą. Unosiła się nad nią słaba nitka dymu. Ocierasz oczy i odruchowo sprawdzasz czy Twoja księga, dzieło Twojego życia, dalej trzyma się Twojego magicznego pasa. Zamyślona zaczynasz pakować swoje rzeczy. Na krótki moment zapominasz o swoim celu, a w Twoich myślach kłębi się tysiące pytań.</p>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}
 
export default Story;